+component(message)

  padding: 0.5em
  margin: 0em
  font-family: 'Raleway', sans-serif !important
  font-weight: 400

  +part(simple)
    padding: 1em
    font-size: 18px
    color: $color-black

  +part(neutral)
    padding: 1em
    font-size: 18px
    color: $color-black
    background-color: $color-light-green

  +part(error)
    background-color: $color-red
    font-size: 20px
    color: $color-white
    +border-radius(1em)

  +part(success)
    background-color: $color-blue
    font-size: 20px
    color: $color-white
    +border-radius(1em)
